import React from 'react';
import { default as MuiZoom, ZoomProps } from '@mui/material/Zoom';

const Zoom = React.forwardRef<HTMLElement, ZoomProps>( ( props, ref ) => {
	return (
		<MuiZoom
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ZoomProps>;

export default Zoom;
